.section {
   margin-top: 70px; /* Add some space to account for the fixed navbar */
}

h2 {
    letter-spacing: 0.1rem;
    text-transform: capitalize;
    line-height:  1.250;
    margin-bottom: .75rem;


}
h1,
h3,
h4 {
    letter-spacing: 0.1rem;
    text-transform: capitalize;
    line-height:  1.250;
    margin-bottom: 0.75rem;

}

.section {
    width: 90vw;
    margin: 5 rem auto;
    max-width:1170px;
}

@media screen and ( min-width: 992px) {
    .section{
        width:95vw;
    }
}

.icon {
     font-size:6rem;
     color:white;
}



.section-center {
   margin: 0 auto;
   margin-top: -5rem;
   margin-left: 25%;
   margin-right: 0%;
   width: 80vw;
   margin-bottom: 300px;
   height: 600px;
   max-width: 800px;
   text-align: center;
   position: relative;
   display: flex;
   overflow: hidden;
}

 article {
    position: absolute;
    top: 0;
    left:0;
    width:100%;
    height: 100%;
    opacity: 0;
    transition:  all 0.3s linear;
 }

 article.activeSlide {
    opacity: 1;
    transform: translateX(0);
 }

 .article.lastSlide{
    transform: translateX(-100%);
 }

 .article.nextSlide{
    transform: translateX(100%);
 }

 .person-img{
    border-radius:100%;
    margin-bottom: 1rem;
    width: 250px;
    height: 250px;
    object-fit: cover;
    border: 4px solid hsl(210, 31%, 80%);
    box-shadow: 0 5px 15px rgba(0,0,0,0,2);
 }

 article h4{
    text-transform: uppercase;
    color:white;
    font-weight: 500;
    margin-bottom:0.25rem;
 }

 .title{
    text-transform: capitalize;
    font-size: 30px;
    margin-bottom: 0.75rem;
    color: rgb(213, 216, 218)
 }
 
 .title {
    text-align: center;
    margin-bottom: 2rem;

 }

 .title h2{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;

 }



 .text {
    max-width: 42rem;
    margin: 0 auto;
    margin-top: 2rem;
    height:10rem ;
    line-height:  1;
    color: rgb(253, 253, 253);
    
}


.prev, next {
   position: absolute;
   top: 200px;
   transform: translateY(-50%);
   background: hsl(210,22%,49%);
   color : #fff;
   width: 1.25rem;
   height: 1.25 rem;
   display : grid;
   place-items:center;
   border-color: transparent;
   font-size:  1rem;
   border-radius:0.25rem;
   cursor: pointer;
transition:all 0.3s linear;

}

.prev:hover,
.next.hover{
   background: hsl(21,62%,45%);
}

.prev {
   left:0;
}

.next {
   right:0;
}

@media (min-width:800px){
   .text{
       max-width:  45rem;
   }
}

.prev,
.next{
   width:2rem;
   height: 2rem;
   font-size:  1.5rem;
}

.activeSlide {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}
.nextSLide, .lastSlide {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}














