table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    border: 1px solid white;
    padding: 8px;
    text-align: left;
    color: white;
}

th {
    background-color: black;
    font-size: 20px;
}

#export-btn {
    background-color : black;
    color:WHite;
    border: none;
    padding: 16px 20px;
    font-size: 55%;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 8px;
}

#export-btn:hover {
    background-color: white;
    color: black;
   
}

#del-btn{
    background-color : black;
    color:WHite;
    border: none;
    padding: 8px 20px;
    font-size: 15px;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 8px;

}
#del-btn:hover{
    background-color: white;
    color: black;
    
}

#title-css{
    text-align: center;
    position:relative;
     top:-56px; 
     left:0px;
      margin-left:0px;
       margin-right:0px;
}
.dashboard {
    margin: 50px auto;
    width: 80%;
    text-align: center;
    
}

