

form {
  width: 600px;
  margin: 50px auto;
  text-align: center;
  padding: 20px;
  border: 5px solid white;
  border-radius: 5px;
  background-color: black;
  
}

/* Form inputs */
form input, form textarea {
  width: 80%;
  padding: 12px 20px;
  margin: 8px auto;
  display: block;
  border: 1px solid grey;
  border-radius: 4px;
  box-sizing: border-box;
  text-align:center;
  font-size: 18px;
  font-family: Arial, sans-serif;
  width: 79%;
}

/* Form submit button */
form button[type="submit"] {
  width: 80%;
  background-color: black;
  color: white;
  padding: 14px 0px;
  margin: 8px auto;
  border: 5px solid white;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  text-align: center;
  font-size: 18px;
  font-family: Arial, sans-serif;
}

form button[type="submit"]:hover {
  background-color: white;
  color: black;
  font-family: Arial, sans-serif;
  font-style: italic;
  font-size: 20px;
}

/* Form heading */
form h2 {
  margin-bottom: 20px;
  text-align: center;
  font-size: 24px;
  font-family: Arial, sans-serif;
}

