.header-1 {
    display: flex;
    justify-content: space-between;
    align-items: top;
    padding: 1rem;
    position: static;
    width: 100%;
    height: 90px;
    z-index: 10;
    background-color: var(--main-color);
    transition: background-color 0.3s ease-in-out;
}

.header-bg-1{
    background-color: #333;
}

.nav-menu {
    display: flex;
    list-style: none;
    color : rgb(56, 54, 54)
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    transition: 0.3s;
    
    
}

.nav-menu li a:hover {
    
    color: var(--hover-color);
    border: 3px solid white;
    border-radius: 10px;
    padding: 10px 7px;

}

.nav-menu .active {
    color: var(--active-color);
}

.hamburger {
    display: none;
}

.Htag {
    display: flex;
    align-items: center;
  }
  
  .FaHome {
    margin-right: 1.5rem;
  }

h1{
    margin: center;
}
.hamburger i {
    color: #fff;
    font-size: 2rem;
    cursor: pointer;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        background: var(--main-color);
        position: absolute;
        top: 90px;
        left: -100%;
        z-index: -3;
        transition: 0.5s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem 0;
font-size: 50%;
    }
    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger {
        display: initial;
    }

    .hamburger i {
        color: #fff;
        font-size: 2rem;
        cursor: pointer;
    }
}

