.technoid3 {

    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height:  60vh;
    position: relative;
    transition: opacity 10 ease-in-out;
}



   


.technoid-Img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center top;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.mask2 {
    background-size: cover;
    background-position : center; 
    /* height: "100vh";
    width: "100vw"; */
    height: 200px;
    width: 250px;

    padding: 0%;
}

.heading {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.technoid3 h1  {
    color: black;
    position: absolute;
    top: -199px;
    font-size: 4.4rem;
    text-align: center;
    transition: opacity 10 ease-in-out;
}
h1 .about_h1 {
    color: white;
    position: relative;
    top: -299px;
    font-size: 2.4rem;
    text-align: center;
    transition: opacity 10 ease-in-out;
}

#about_tag {
    width: 980px;
    color: black;
    position: absolute;
    top: -128px;
    font-size: 3rem;
    font-family: ui-sans-serif;
    text-align: center;
    transition: opacity 10 ease-in-out;
}
#consult {
    filter: brightness(103%);
    width: 1352px;
    position: absolute;
    top: 139px;
    }



.fadeIn {
    filter: brightness(103%);
    background: white;
    opacity: 3;
}

.fadeOut {
    opacity: 0;
}

@media screen and (max-width: 640px) {
    .technoid2 h1 {
        font-size: 2rem;
    }
}
